@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.input {
  @apply w-full px-4 py-2 rounded-md border border-slate-200 bg-white/90 text-slate-700 placeholder-slate-400 transition;
  @apply focus:border-blue-400 focus:bg-blue-50 focus:outline-none;
  @apply hover:border-blue-200;
}
